"use client";

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Progress } from '@/components/ui/progress';
import { Textarea } from '@/components/ui/textarea';
import type { DashboardMetrics, ChurnPrediction, NaturalLanguageQuery } from '@/types';

export default function PeopleAIDashboard() {
  const [metrics, setMetrics] = useState<DashboardMetrics | null>(null);
  const [predictions, setPredictions] = useState<ChurnPrediction[]>([]);
  const [query, setQuery] = useState('');
  const [queryResult, setQueryResult] = useState<NaturalLanguageQuery | null>(null);
  const [loading, setLoading] = useState(false);
  const [queryTemplates, setQueryTemplates] = useState<any[]>([]);

  useEffect(() => {
    loadDashboardData();
    loadQueryTemplates();
  }, []);

  const loadDashboardData = async () => {
    try {
      // Load dashboard metrics
      const metricsResponse = await fetch('/api/employees?metrics=true');
      const metricsData = await metricsResponse.json();
      if (metricsData.success) {
        setMetrics(metricsData.data);
      }

      // Load high-risk predictions
      const predictionsResponse = await fetch('/api/predictions?riskLevel=HIGH&limit=10');
      const predictionsData = await predictionsResponse.json();
      if (predictionsData.success) {
        setPredictions(predictionsData.data);
      }
    } catch (error) {
      console.error('Failed to load dashboard data:', error);
    }
  };

  const loadQueryTemplates = async () => {
    try {
      const response = await fetch('/api/queries');
      const data = await response.json();
      if (data.success) {
        setQueryTemplates(data.data);
      }
    } catch (error) {
      console.error('Failed to load query templates:', error);
    }
  };

  const handleQuerySubmit = async () => {
    if (!query.trim()) return;

    setLoading(true);
    try {
      const response = await fetch('/api/queries', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ query, userId: 'demo-user' }),
      });

      const data = await response.json();
      if (data.success) {
        setQueryResult(data.data);
      } else {
        alert(`Query failed: ${data.error}`);
      }
    } catch (error) {
      console.error('Query failed:', error);
      alert('Query failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const getRiskBadgeColor = (riskLevel: string) => {
    switch (riskLevel) {
      case 'CRITICAL': return 'bg-red-600 text-white hover:bg-red-700';
      case 'HIGH': return 'bg-red-500 text-white hover:bg-red-600';
      case 'MEDIUM': return 'bg-yellow-500 text-white hover:bg-yellow-600';
      case 'LOW': return 'bg-green-500 text-white hover:bg-green-600';
      default: return 'bg-gray-500 text-white hover:bg-gray-600';
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900">
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-purple-500 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-blue-500 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse"></div>
        <div className="absolute top-40 left-40 w-80 h-80 bg-pink-500 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse"></div>
      </div>
      
      <div className="relative z-10 p-6">
        <div className="max-w-7xl mx-auto space-y-8">
          {/* Enhanced Header */}
          <div className="text-center space-y-4 py-12">
            <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full mb-6 animate-bounce">
              <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center">
                <div className="w-6 h-6 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full"></div>
              </div>
            </div>
            <h1 className="text-6xl font-bold bg-gradient-to-r from-white via-purple-200 to-pink-200 bg-clip-text text-transparent">
              People AI Platform
            </h1>
            <p className="text-xl text-gray-300 max-w-2xl mx-auto leading-relaxed">
              Harness the power of artificial intelligence to predict employee churn, analyze engagement patterns, and make data-driven HR decisions
            </p>
            <div className="flex justify-center space-x-4 mt-8">
              <div className="px-4 py-2 bg-purple-500/20 rounded-full border border-purple-500/30 text-purple-200 text-sm">
                🤖 ML-Powered Predictions
              </div>
              <div className="px-4 py-2 bg-blue-500/20 rounded-full border border-blue-500/30 text-blue-200 text-sm">
                💬 Natural Language Queries
              </div>
              <div className="px-4 py-2 bg-pink-500/20 rounded-full border border-pink-500/30 text-pink-200 text-sm">
                📊 Real-time Analytics
              </div>
            </div>
          </div>

          {/* Enhanced Dashboard Metrics */}
          {metrics && (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              <Card className="bg-white/10 backdrop-blur-lg border-white/20 hover:bg-white/15 transition-all duration-300 hover:scale-105">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-300">Total Employees</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-white">{metrics.total_employees}</div>
                  <div className="text-xs text-gray-400 mt-1">Active workforce</div>
                </CardContent>
              </Card>

              <Card className="bg-white/10 backdrop-blur-lg border-white/20 hover:bg-white/15 transition-all duration-300 hover:scale-105">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-300">High Risk</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-red-400">{metrics.high_risk_count}</div>
                  <p className="text-xs text-red-300 mt-1">Immediate attention needed</p>
                  <div className="w-full bg-red-900/30 rounded-full h-2 mt-2">
                    <div 
                      className="bg-red-500 h-2 rounded-full transition-all duration-500" 
                      style={{ width: `${(metrics.high_risk_count / metrics.total_employees) * 100}%` }}
                    ></div>
                  </div>
                </CardContent>
              </Card>

              <Card className="bg-white/10 backdrop-blur-lg border-white/20 hover:bg-white/15 transition-all duration-300 hover:scale-105">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-300">Avg Engagement</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-blue-400">{metrics.avg_engagement_score}/10</div>
                  <div className="w-full bg-blue-900/30 rounded-full h-2 mt-2">
                    <div 
                      className="bg-blue-500 h-2 rounded-full transition-all duration-500" 
                      style={{ width: `${metrics.avg_engagement_score * 10}%` }}
                    ></div>
                  </div>
                </CardContent>
              </Card>

              <Card className="bg-white/10 backdrop-blur-lg border-white/20 hover:bg-white/15 transition-all duration-300 hover:scale-105">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-gray-300">Retention Rate</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-green-400">{(metrics.retention_rate * 100).toFixed(1)}%</div>
                  <div className="w-full bg-green-900/30 rounded-full h-2 mt-2">
                    <div 
                      className="bg-green-500 h-2 rounded-full transition-all duration-500" 
                      style={{ width: `${metrics.retention_rate * 100}%` }}
                    ></div>
                  </div>
                </CardContent>
              </Card>
            </div>
          )}

          {/* Enhanced Main Content Tabs */}
          <Tabs defaultValue="predictions" className="space-y-6">
            <TabsList className="grid w-full grid-cols-3 bg-white/10 backdrop-blur-lg border-white/20">
              <TabsTrigger value="predictions" className="data-[state=active]:bg-purple-500 data-[state=active]:text-white text-gray-300">
                🎯 Churn Predictions
              </TabsTrigger>
              <TabsTrigger value="queries" className="data-[state=active]:bg-purple-500 data-[state=active]:text-white text-gray-300">
                💬 AI Queries
              </TabsTrigger>
              <TabsTrigger value="analytics" className="data-[state=active]:bg-purple-500 data-[state=active]:text-white text-gray-300">
                📊 Analytics
              </TabsTrigger>
            </TabsList>

            {/* Enhanced Churn Predictions Tab */}
            <TabsContent value="predictions" className="space-y-6">
              <Card className="bg-white/10 backdrop-blur-lg border-white/20">
                <CardHeader>
                  <CardTitle className="text-white flex items-center gap-2">
                    🚨 High-Risk Employees
                  </CardTitle>
                  <CardDescription className="text-gray-300">
                    Employees with elevated churn risk requiring immediate attention
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="overflow-x-auto">
                    <Table>
                      <TableHeader>
                        <TableRow className="border-white/20">
                          <TableHead className="text-gray-300">Employee ID</TableHead>
                          <TableHead className="text-gray-300">Risk Score</TableHead>
                          <TableHead className="text-gray-300">Risk Level</TableHead>
                          <TableHead className="text-gray-300">Key Risk Factors</TableHead>
                          <TableHead className="text-gray-300">Confidence</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {predictions.map((prediction) => (
                          <TableRow key={prediction.id} className="border-white/10 hover:bg-white/5 transition-colors">
                            <TableCell className="font-medium text-white">{prediction.employee_id}</TableCell>
                            <TableCell className="text-white">
                              <div className="flex items-center gap-2">
                                <span>{(prediction.risk_score * 100).toFixed(1)}%</span>
                                <div className="w-16 bg-gray-700 rounded-full h-2">
                                  <div 
                                    className="bg-red-500 h-2 rounded-full transition-all duration-500" 
                                    style={{ width: `${prediction.risk_score * 100}%` }}
                                  ></div>
                                </div>
                              </div>
                            </TableCell>
                            <TableCell>
                              <Badge className={getRiskBadgeColor(prediction.risk_level)}>
                                {prediction.risk_level}
                              </Badge>
                            </TableCell>
                            <TableCell className="max-w-xs">
                              <div className="space-y-1">
                                {prediction.risk_factors.slice(0, 2).map((factor, index) => (
                                  <div key={index} className="text-xs bg-white/10 px-2 py-1 rounded text-gray-300">
                                    {factor}
                                  </div>
                                ))}
                              </div>
                            </TableCell>
                            <TableCell className="text-white">{(prediction.confidence * 100).toFixed(1)}%</TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            {/* Enhanced Natural Language Queries Tab */}
            <TabsContent value="queries" className="space-y-6">
              <Card className="bg-white/10 backdrop-blur-lg border-white/20">
                <CardHeader>
                  <CardTitle className="text-white flex items-center gap-2">
                    🤖 Ask Questions About Your Data
                  </CardTitle>
                  <CardDescription className="text-gray-300">
                    Use natural language to query your HR data and get AI-powered insights
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-3">
                    <label className="text-sm font-medium text-gray-300">Your Question</label>
                    <Textarea
                      placeholder="e.g., Show me all employees with high churn risk in the Engineering department"
                      value={query}
                      onChange={(e) => setQuery(e.target.value)}
                      className="min-h-[100px] bg-white/5 border-white/20 text-white placeholder:text-gray-400 focus:border-purple-500"
                    />
                  </div>
                  
                  <Button 
                    onClick={handleQuerySubmit} 
                    disabled={loading || !query.trim()}
                    className="w-full bg-gradient-to-r from-purple-500 to-pink-500 hover:from-purple-600 hover:to-pink-600 text-white font-medium py-3 transition-all duration-300 hover:scale-105"
                  >
                    {loading ? (
                      <div className="flex items-center gap-2">
                        <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                        Processing...
                      </div>
                    ) : (
                      '✨ Ask Question'
                    )}
                  </Button>

                  {/* Enhanced Query Templates */}
                  <div className="space-y-3">
                    <label className="text-sm font-medium text-gray-300">Quick Questions</label>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                      {queryTemplates.map((template, index) => (
                        <Button
                          key={index}
                          variant="outline"
                          size="sm"
                          onClick={() => setQuery(template.query)}
                          className="text-left justify-start h-auto p-4 bg-white/5 border-white/20 hover:bg-white/10 text-white hover:text-white transition-all duration-300 hover:scale-105"
                        >
                          <div>
                            <div className="font-medium">{template.title}</div>
                            <div className="text-xs text-gray-400 mt-1">{template.description}</div>
                          </div>
                        </Button>
                      ))}
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Enhanced Query Results */}
              {queryResult && (
                <Card className="bg-white/10 backdrop-blur-lg border-white/20 animate-in slide-in-from-bottom-4 duration-500">
                  <CardHeader>
                    <CardTitle className="text-white flex items-center gap-2">
                      ✨ Query Results
                    </CardTitle>
                    <CardDescription className="text-gray-300">
                      Results for: "{queryResult.query}"
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-6">
                    <div>
                      <h4 className="font-medium mb-3 text-white">💡 AI Explanation</h4>
                      <p className="text-sm text-gray-300 bg-blue-500/20 p-4 rounded-lg border border-blue-500/30">
                        {queryResult.explanation}
                      </p>
                    </div>
                    
                    <div>
                      <h4 className="font-medium mb-3 text-white">📊 Data Results</h4>
                      <div className="bg-white/5 p-4 rounded-lg border border-white/20 overflow-auto">
                        <pre className="text-xs text-gray-300">
                          {JSON.stringify(queryResult.results, null, 2)}
                        </pre>
                      </div>
                    </div>

                    <div>
                      <h4 className="font-medium mb-3 text-white">🔍 Generated SQL</h4>
                      <div className="bg-gray-900/50 p-4 rounded-lg border border-gray-700 overflow-auto">
                        <pre className="text-xs text-green-400">
                          {queryResult.sql_generated}
                        </pre>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              )}
            </TabsContent>

            {/* Enhanced Analytics Tab */}
            <TabsContent value="analytics" className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <Card className="bg-white/10 backdrop-blur-lg border-white/20">
                  <CardHeader>
                    <CardTitle className="text-white flex items-center gap-2">
                      📈 Risk Distribution
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    {metrics && (
                      <div className="space-y-4">
                        <div className="flex justify-between items-center p-3 bg-red-500/20 rounded-lg border border-red-500/30">
                          <span className="text-sm text-red-200">Critical Risk</span>
                          <span className="text-sm font-medium text-red-200">0</span>
                        </div>
                        <div className="flex justify-between items-center p-3 bg-red-400/20 rounded-lg border border-red-400/30">
                          <span className="text-sm text-red-200">High Risk</span>
                          <span className="text-sm font-medium text-red-200">{metrics.high_risk_count}</span>
                        </div>
                        <div className="flex justify-between items-center p-3 bg-yellow-500/20 rounded-lg border border-yellow-500/30">
                          <span className="text-sm text-yellow-200">Medium Risk</span>
                          <span className="text-sm font-medium text-yellow-200">{metrics.medium_risk_count}</span>
                        </div>
                        <div className="flex justify-between items-center p-3 bg-green-500/20 rounded-lg border border-green-500/30">
                          <span className="text-sm text-green-200">Low Risk</span>
                          <span className="text-sm font-medium text-green-200">{metrics.low_risk_count}</span>
                        </div>
                      </div>
                    )}
                  </CardContent>
                </Card>

                <Card className="bg-white/10 backdrop-blur-lg border-white/20">
                  <CardHeader>
                    <CardTitle className="text-white flex items-center gap-2">
                      🎯 Model Performance
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-4">
                      <div className="flex justify-between items-center p-3 bg-purple-500/20 rounded-lg border border-purple-500/30">
                        <span className="text-sm text-purple-200">Accuracy</span>
                        <span className="text-sm font-medium text-purple-200">84.7%</span>
                      </div>
                      <div className="flex justify-between items-center p-3 bg-blue-500/20 rounded-lg border border-blue-500/30">
                        <span className="text-sm text-blue-200">Precision</span>
                        <span className="text-sm font-medium text-blue-200">82.3%</span>
                      </div>
                      <div className="flex justify-between items-center p-3 bg-pink-500/20 rounded-lg border border-pink-500/30">
                        <span className="text-sm text-pink-200">Recall</span>
                        <span className="text-sm font-medium text-pink-200">79.1%</span>
                      </div>
                      <div className="flex justify-between items-center p-3 bg-indigo-500/20 rounded-lg border border-indigo-500/30">
                        <span className="text-sm text-indigo-200">F1 Score</span>
                        <span className="text-sm font-medium text-indigo-200">80.7%</span>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </TabsContent>
          </Tabs>
        </div>
      </div>
    </div>
  );
}
